  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "IBM Plex Sans", sans-serif;
  src: url("IBMPlexSans-Medium.14c9ca0b.ttf");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "IBM Plex Sans", sans-serif;
  src: url("IBMPlexSans-SemiBold.868bf5e1.ttf");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "IBM Plex Sans", sans-serif;
  src: url("IBMPlexSans-Bold.a7d2e24b.ttf");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: Montserrat, sans-serif;
  src: url("Montserrat-Regular.5c4ab150.ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Montserrat, sans-serif;
  src: url("Montserrat-Bold.1b1f71c4.ttf");
  font-weight: 700;
  font-style: normal;
}

h1 {
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 20px;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 1;
}

@media (width >= 640px) {
  h1 {
    text-align: left;
    padding-bottom: 38px;
    font-size: 54px;
    line-height: 1.2;
  }
}

@media (width >= 1280px) {
  h1 {
    padding-bottom: 0;
    font-size: 72px;
  }
}

h2 {
  text-align: center;
  margin: 0;
  padding-bottom: 40px;
  font-family: Montserrat, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
}

@media (width >= 640px) {
  h2 {
    text-align: left;
    font-size: 48px;
  }
}

@media (width >= 1280px) {
  h2 {
    padding-bottom: 56px;
  }
}

h3 {
  text-align: left;
  margin-block: 15px 11px;
  padding-bottom: 5px;
  font-family: Montserrat, sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 1;
  position: relative;
}

@media (width >= 640px) {
  h3 {
    font-size: 36px;
  }
}

a {
  color: #0f0e08;
  cursor: pointer;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

.word-wrap {
  width: 100px;
}

button {
  cursor: pointer;
}

@media (width >= 640px) {
  .main {
    padding-top: 80px;
  }
}

@media (width >= 1280px) {
  .main {
    padding-top: 220px;
  }
}

.page {
  text-align: center;
  scroll-behavior: smooth;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.page__body {
  background: #eee;
  margin: 0;
}

@media (width >= 1440px) {
  .page__body {
    position: relative;
  }
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  background-color: #18181766;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  z-index: 3;
  pointer-events: all;
  overflow-y: auto;
  transform: translateX(0);
}

.page__content {
  background-color: #1a5a4c;
  padding: 27px 20px;
}

.container {
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .container {
    padding-inline: 55px;
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.icon-logo {
  width: 55px;
  height: 61px;
}

.icon-logo-link {
  margin-right: 110px;
}

@media (width >= 640px) {
  .icon-logo-link {
    width: 220px;
    margin-right: 110px;
  }
}

@media (width >= 1024px) {
  .icon-logo-link {
    margin-right: 220px;
  }
}

@media (width >= 1280px) {
  .icon-logo-link {
    margin-right: 450px;
  }
}

.icon-logo-link--footer-margin {
  margin-right: 0;
}

.icon-cross {
  width: 24px;
  height: 24px;
}

.footer-icon {
  width: 24px;
  height: 24px;
  transition: transform .3s;
}

.footer-icon:hover {
  transform: scale(1.3);
}

.top-bar {
  padding-bottom: 100%;
}

@media (width >= 640px) {
  .top-bar {
    padding-bottom: 0;
  }
}

.top-bar__menu-logo {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header {
  z-index: 1;
  padding-block: 9px 70px;
}

@media (width >= 1440px) {
  .header {
    max-width: 1440px;
    margin: 0 auto;
  }
}

@media (width >= 640px) {
  .header__bottom {
    margin-top: 64px;
    display: flex;
  }
}

@media (width >= 1280px) {
  .header__bottom {
    margin-top: 143px;
  }
}

.header__background-container {
  z-index: -1;
  position: absolute;
  top: 0;
  right: 0;
}

.header__background {
  width: 100%;
}

@media (width >= 640px) {
  .header__background {
    max-width: 374px;
    height: 384px;
  }
}

@media (width >= 1024px) {
  .header__background {
    max-width: 525px;
    height: 455px;
  }
}

@media (width >= 1280px) {
  .header__background {
    max-width: 725px;
    height: 655px;
  }
}

.header__bottom-content {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media (width >= 640px) {
  .header__bottom-content {
    align-items: flex-start;
  }
}

@media (width >= 1280px) {
  .header__bottom-content {
    align-items: flex-end;
    margin-left: 20px;
  }
}

.header__arrow-button {
  position: relative;
}

.header__button {
  text-transform: uppercase;
  border: 1px solid #1a5a4c;
  width: 100%;
  padding: 15.4px 130px 15.4px 80px;
  font-family: IBM Plex Sans, sans-serif;
  font-weight: 500;
}

@media (width >= 640px) {
  .header__button {
    padding: 25px 138px 26px 68px;
  }
}

@media (width >= 1280px) {
  .header__button {
    margin-top: 89px;
  }
}

.header__button:hover {
  border-color: #4e4e4e;
}

.header__button:hover:after {
  background-color: #4e4e4e;
}

.header__button:after {
  content: "";
  background-color: #1a5a4c;
  background-image: url("arrow-white-3@.d32f66aa.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  width: 50px;
  height: 50px;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (width >= 640px) {
  .header__button:after {
    width: 70px;
    height: 70px;
  }
}

.header__desktop-block {
  display: none;
  position: absolute;
  top: 53%;
  left: 55px;
}

@media (width >= 1280px) {
  .header__desktop-block {
    text-transform: uppercase;
    color: #687480;
    font-weight: 500;
    display: block;
    transform: rotate(270deg);
  }
}

@media (width >= 1440px) {
  .header__desktop-block {
    position: relative;
    top: 1rem;
    left: -25rem;
  }
}

.header__desktop-content {
  position: relative;
}

.header__desktop-content:before {
  content: "";
  background-color: #687480;
  width: 1px;
  height: 70px;
  position: absolute;
  top: -23px;
  left: -55px;
  transform: rotate(90deg);
}

.menu {
  color: #fff;
  height: 100vh;
}

@media (width >= 640px) {
  .menu {
    height: 150vh;
  }

  .menu__nav {
    gap: 77px;
    display: flex;
  }
}

@media (width >= 1280px) {
  .menu__nav {
    justify-content: space-around;
  }
}

.nav__top {
  align-items: flex-start;
  gap: 28px;
  margin-bottom: 24px;
  display: flex;
  position: relative;
}

@media (width >= 640px) {
  .nav__top {
    gap: 48px;
  }
}

@media (width >= 1280px) {
  .nav__top {
    gap: 102px;
    padding-right: 150px;
  }
}

.nav__top:after {
  content: "";
  background: #ffffff80;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width >= 640px) {
  .nav__top:after {
    content: none;
    display: none;
  }
}

.nav__list {
  text-align: left;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px 28px;
  padding-bottom: 24px;
  display: grid;
}

@media (width >= 1280px) {
  .nav__list {
    gap: 30px 50px;
  }
}

.nav__list--footer {
  padding-block: 40px 80px;
  gap: 20px;
  width: 100%;
  max-width: 460px;
  margin: 0 auto;
  padding-right: 15px;
}

.nav__link {
  color: #fff;
  font-weight: 500;
  line-height: 1;
}

.nav__link:hover {
  transition: color .3s;
}

.nav__link:hover:hover {
  color: #687480;
}

@media (width >= 1280px) {
  .nav__link {
    font-size: 22px;
  }
}

.nav__link--footer {
  color: #0f0e08;
  text-wrap: nowrap;
  font-size: 14px;
  font-weight: 400;
}

@media (width >= 640px) {
  .nav__schedule {
    align-items: end;
    gap: 26px;
    display: flex;
  }
}

.nav__time-work {
  text-align: left;
  width: 100%;
  position: relative;
}

@media (width >= 640px) {
  .nav__time-work--before:before {
    content: "";
    background: #ffffff80;
    width: 1px;
    height: 100%;
    position: absolute;
    left: -38px;
  }
}

@media (width >= 640px) and (width >= 1280px) {
  .nav__time-work--before:before {
    left: -108px;
  }
}

.nav__item-margin {
  margin-bottom: 20px;
}

.nav__free-day {
  margin-block: 16px 27px;
}

@media (width >= 640px) {
  .nav__free-day {
    flex-shrink: 1.1;
    margin-block: 20px 27px;
  }
}

.nav__info-freeday {
  text-align: left;
}

.nav__contacts-list {
  text-align: left;
  width: 100%;
  max-width: 220px;
}

.nav__item-hover {
  transition: color .3s;
}

.nav__item-hover:hover {
  color: #1a5a4c;
}

.actions {
  padding-bottom: 60px;
}

@media (width >= 1440px) {
  .actions {
    max-width: 1440px;
    margin: 0 auto;
  }
}

@media (width >= 640px) {
  .actions {
    padding-bottom: 110px;
  }
}

.actions__container {
  row-gap: 60px;
  display: grid;
}

@media (width >= 1280px) {
  .actions__container {
    row-gap: 104px;
  }
}

.actions__block {
  width: 100%;
  max-width: 460px;
}

@media (width >= 1280px) {
  .actions__block {
    max-width: 370px;
  }
}

@media (width >= 1440px) {
  .actions__block {
    margin: 0 auto;
    display: block;
  }
}

.actions__block--title-and-event {
  background-color: #fff;
  position: absolute;
  bottom: -100px;
  box-shadow: 0 2px #1a5a4c40;
}

@media (width >= 640px) {
  .actions__block--title-and-event {
    padding: 20px 80px 20px 20px;
    transform: translate(13%, 15%);
  }
}

@media (width >= 1024px) {
  .actions__block--title-and-event {
    padding: 20px 80px 20px 20px;
    transform: translate(35%, 15%);
  }
}

@media (width >= 1280px) {
  .actions__block--title-and-event {
    padding: 45px 100px 50px;
    transform: translate(50%, 35%);
  }
}

@media (width >= 1440px) {
  .actions__block--title-and-event {
    transform: translate(75%, 40%);
  }
}

.actions__card--position {
  position: relative;
}

@media (width >= 1280px) {
  .actions__card {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.actions__images {
  width: 100%;
  transition: transform .3s;
}

.actions__images:hover {
  transform: scale(1.1);
}

@media (width >= 1280px) {
  .actions__images {
    max-width: 670px;
  }
}

.actions__img-full-width {
  width: calc(100% + 40px);
  margin: -20px;
}

@media (width >= 640px) {
  .actions__img-full-width {
    width: 100%;
    height: 390px;
  }
}

@media (width >= 1280px) {
  .actions__img-full-width {
    margin: -55px;
    width: calc(100% + 110px);
    height: 550px;
    margin-top: 20px;
  }
}

.actions__action {
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.actions__action--inline {
  padding-inline: 20px;
}

@media (width >= 1280px) {
  .actions__action--inline {
    padding-inline: 15px 35px;
  }
}

.actions__type {
  color: #4e4e4e;
  text-transform: uppercase;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}

@media (width >= 640px) {
  .actions__type {
    font-size: 14px;
  }
}

.actions__date {
  color: #1a5a4c;
  text-align: left;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

@media (width >= 640px) {
  .actions__date {
    font-size: 14px;
  }
}

.actions__title-flex {
  align-items: center;
  gap: 10px;
  display: flex;
}

@media (width >= 640px) {
  .actions__title-flex {
    gap: 20px;
  }
}

.actions__red-square {
  background-color: #f14a27;
  width: 15px;
  height: 15px;
}

.actions__title-center {
  text-align: center;
  margin-bottom: 20px;
}

.actions__information {
  text-align: left;
  color: #4e4e4e;
  width: 100%;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  line-height: 24px;
  display: block;
}

.actions-roman {
  padding-bottom: 183px;
}

@media (width >= 640px) {
  .actions-roman {
    padding-bottom: 208px;
  }
}

@media (width >= 1280px) {
  .actions-roman {
    padding-bottom: 302px;
  }
}

.follow {
  color: #fff;
  background-image: url("follow-background-3@.d69fc0c9.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-inline: -20px;
  padding-block: 88px;
}

@media (width >= 640px) {
  .follow {
    margin-inline: -34px;
  }
}

@media (width >= 1280px) {
  .follow {
    margin-inline: -55px;
  }
}

.follow__content {
  margin: 0 auto;
  padding-inline: 20px;
}

@media (width >= 640px) {
  .follow__content {
    width: 100%;
