.form {
  justify-content: center;
  margin-top: 50px;
  margin-right: 25px;
  display: flex;
}

.form__button {
  position: relative;
}

.form__button:after {
  content: "";
  background-color: #f14a27;
  background-image: url("Vector.5cbf34ce.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 8px 12px;
  width: 50px;
  height: 50px;
  position: absolute;
  bottom: -2px;
  left: -2px;
}

@media (width >= 640px) {
  .form__button:after {
