.form__input {
  border: none;
  outline: none;
  padding: 16.5px 24px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  line-height: 14px;
}

@media (width >= 640px) {
  .form__input {
    margin-right: 16px;
    padding: 28px 180px 28px 24px;
  }
}

@media (width >= 1280px) {
  .form__input {
    padding: 28px 300px 28px 32px;
  }
}

input:-webkit-autofill {
  -webkit-text-fill-color: #0f0e08;
  border: #fff;
  transition: background-color 5000s ease-in-out;
  -webkit-box-shadow: inset 0 0 0 1000px #fff;
}

input:-webkit-autofill:hover {
  -webkit-text-fill-color: #0f0e08;
  border: #fff;
  transition: background-color 5000s ease-in-out;
  -webkit-box-shadow: inset 0 0 0 1000px #fff;
}

input:-webkit-autofill:focus {
  -webkit-text-fill-color: #0f0e08;
  border: #fff;
  transition: background-color 5000s ease-in-out;
  -webkit-box-shadow: inset 0 0 0 1000px #fff;
}

.gallery {
  padding-bottom: 100px;
}

@media (width >= 1440px) {
  .gallery {
    max-width: 1440px;
    margin: 0 auto;
  }
}

.gallery__grid-slides {
  display: none;
}

@media (width >= 1280px) {
  .gallery__grid-slides {
    max-width: 1170px;
    margin: 0 auto;
    display: block;
  }
}

.gallery__slides {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  gap: 30px;
  display: grid;
}

@media (width >= 640px) {
  .gallery__slides {
    --columns: 6;
  }
}

@media (width >= 1280px) {
  .gallery__slides {
    --columns: 12;
  }
}

.gallery__slide {
  width: 100%;
  transition: transform .3s;
}

.gallery__slide:hover {
  transform: scale(1.05);
}

.gallery__slide--1 {
  max-width: 370px;
  max-height: 600px;
}

.gallery__slide--2 {
  max-width: 770px;
  max-height: 600px;
}

.gallery__slide--3 {
  max-width: 770px;
  max-height: 360px;
}

.gallery__slide--4 {
  max-width: 370px;
  max-height: 360px;
}

@media (width >= 1280px) {
  .gallery__slide--wide {
    grid-column: span 8;
  }
}

.gallery__slide--square {
  height: 100%;
}

@media (width >= 1280px) {
  .gallery__slide--square {
    grid-column: span 4;
  }
}

.gallery__js-slider {
  display: block;
}

@media (width >= 1280px) {
  .gallery__js-slider {
    display: none;
  }
}

.gallery__container {
  min-width: 300px;
  max-width: 768px;
  display: block;
  overflow: hidden;
}

.gallery__image {
  width: 280px;
  height: 410px;
  transition: transform .3s;
}

.gallery__image:hover {
  transform: scale(1.1);
}

@media (width >= 640px) {
  .gallery__image {
    width: 340px;
    height: 543px;
  }
}

.gallery__list {
  justify-content: flex-start;
  gap: 20px;
  display: flex;
}

.pagination {
