.carousel-button {
  display: none;
}

.footer {
  padding-block: 50px;
  font-size: 14px;
}

@media (width >= 1440px) {
  .footer {
    max-width: 1440px;
    margin: 0 auto;
  }
}

@media (width >= 640px) {
  .footer__content {
    --columns: 2;
    grid-template-columns: repeat(var(--columns), 1fr);
    gap: 30px;
    display: grid;
  }
}

@media (width >= 640px) and (width >= 640px) {
  .footer__content {
    --columns: 6;
  }
}

@media (width >= 640px) and (width >= 1280px) {
  .footer__content {
    --columns: 12;
  }
}

@media (width >= 640px) {
  .footer__top {
    gap: 40px;
    display: flex;
  }
}

@media (width >= 1280px) {
  .footer__top {
    gap: 130px;
